.container {
  width: 100%;
  height: 100%;
  padding: 2rem 1rem;
  background: linear-gradient(
    135deg,
    rgba(9, 9, 11, 1) 0%,
    rgba(24, 24, 27, 0.5) 100%
  );
  position: relative;
  overflow-y: auto;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(
      circle at 20% 50%,
      rgba(34, 211, 238, 0.03) 0%,
      transparent 50%
    ),
    radial-gradient(
      circle at 80% 80%,
      rgba(236, 72, 153, 0.03) 0%,
      transparent 50%
    );
  pointer-events: none;
}

.shareboardLayout {
  display: flex;
  gap: 2rem;
  width: 100%;
  max-width: 1400px;
  position: relative;
  z-index: 1;
  flex-wrap: wrap;
  align-items: flex-start;
}

/* Grid Container */
.gridContainer {
  flex: 1;
  min-width: 400px;
}

.sectionHeader {
  margin-bottom: 2rem;
}

.sectionTitle {
  font-size: 1.75rem;
  font-weight: 700;
  color: white;
  margin: 0 0 0.5rem 0;
  letter-spacing: -0.02em;
}

.sectionSubtitle {
  font-size: 0.95rem;
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
}

.shareboardsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  gap: 1.5rem;
}

/* Shareboard Card */
.shareboardCard {
  background: rgba(24, 24, 27, 0.6);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(63, 63, 70, 0.4);
  border-radius: 1.5rem;
  padding: 1.5rem;
  position: relative;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.shareboardCard::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  border-radius: 1.5rem 1.5rem 0 0;
}

.shareboardCard:hover {
  background: rgba(39, 39, 42, 0.8);
  border-color: rgba(34, 211, 238, 0.4);
  transform: translateY(-4px);
  box-shadow: 0 15px 40px rgba(34, 211, 238, 0.15);
}

.cardHeader {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.userAvatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.2rem;
  flex-shrink: 0;
}

.cardMeta {
  flex: 1;
  min-width: 0;
}

.cardTitle {
  font-size: 1.1rem;
  font-weight: 700;
  color: white;
  margin: 0;
  word-break: break-word;
}

.cardCreator {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.5);
  margin: 0.25rem 0 0 0;
}

.cardDescription {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.6);
  line-height: 1.5;
  margin: 0;
}

.nowPlaying {
  background: rgba(34, 211, 238, 0.05);
  border: 1px solid rgba(34, 211, 238, 0.2);
  border-radius: 1rem;
  padding: 1rem;
}

.nowPlayingLabel {
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: rgb(34, 211, 238);
  font-weight: 600;
  margin: 0 0 0.5rem 0;
}

.nowPlayingTrack {
  font-size: 0.95rem;
  font-weight: 600;
  color: white;
  margin: 0;
}

.nowPlayingArtist {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
  margin: 0.25rem 0 0 0;
}

.progressBarSmall {
  width: 100%;
  height: 2px;
  background: rgba(63, 63, 70, 0.5);
  border-radius: 10px;
  overflow: hidden;
  margin: 0.75rem 0;
  position: relative;
}

.progressFill {
  height: 100%;
  background: linear-gradient(
    90deg,
    rgba(34, 211, 238, 0.8),
    rgba(34, 211, 238, 1)
  );
  border-radius: 10px;
  width: 45%;
}

.songListPreview {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 0.75rem;
}

.songPreviewItem {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
  padding: 0.375rem 0;
  border-bottom: 1px solid rgba(63, 63, 70, 0.2);
}

.songPreviewItem:last-child {
  border-bottom: none;
}

.cardFooter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 0.75rem;
  border-top: 1px solid rgba(63, 63, 70, 0.3);
}

.likeCount {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.thumbsUpButton {
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 0.5rem;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 600;
  transition: all 0.2s ease;
}

.thumbsUpButton:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.4);
  color: white;
}

/* Detail Panel */
.detailPanel {
  flex: 0 0 380px;
  max-height: 800px;
  background: rgba(24, 24, 27, 0.6);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(63, 63, 70, 0.4);
  border-radius: 2.5rem;
  padding: 2rem;
  position: relative;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  position: sticky;
  top: 2rem;
}

.detailPanel::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  border-radius: 2.5rem 2.5rem 0 0;
}

.detailHeader {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.detailAvatar {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.detailMeta {
  flex: 1;
}

.detailTitle {
  font-size: 1.2rem;
  font-weight: 700;
  color: white;
  margin: 0;
}

.detailCreator {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.5);
  margin: 0.25rem 0 0 0;
}

.detailDescription {
  font-size: 0.95rem;
  color: rgba(255, 255, 255, 0.6);
  line-height: 1.6;
  margin: 0;
}

.nowPlayingSection {
  background: rgba(34, 211, 238, 0.08);
  border: 1px solid rgba(34, 211, 238, 0.2);
  border-radius: 1.25rem;
  padding: 1.25rem;
}

.nowPlayingSectionLabel {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: rgb(34, 211, 238);
  font-weight: 600;
  margin: 0 0 0.75rem 0;
}

.nowPlayingTrackLarge {
  font-size: 1.1rem;
  font-weight: 700;
  color: white;
  margin: 0;
}

.nowPlayingArtistLarge {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.5);
  margin: 0.25rem 0 0.75rem 0;
}

.albumSection {
  display: flex;
  gap: 1rem;
  align-items: center;
  padding: 1rem;
  background: rgba(39, 39, 42, 0.5);
  border-radius: 1rem;
  border: 1px solid rgba(63, 63, 70, 0.3);
}

.albumArt {
  width: 80px;
  height: 80px;
  border-radius: 0.75rem;
  overflow: hidden;
  flex-shrink: 0;
  border: 1px solid rgba(63, 63, 70, 0.4);
}

.albumArt img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.albumInfo {
  flex: 1;
  min-width: 0;
}

.albumLabel {
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: rgba(255, 255, 255, 0.5);
  font-weight: 600;
  margin: 0;
}

.albumTitle {
  font-size: 0.95rem;
  font-weight: 600;
  color: white;
  margin: 0.25rem 0 0 0;
}

.playerControls {
  display: flex;
  justify-content: center;
  gap: 1rem;
  padding: 0.75rem 0;
}

.playerButton {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.playerButton:hover {
  background: rgba(255, 255, 255, 0.15);
  color: white;
}

.detailSongList {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  max-height: 200px;
  overflow-y: auto;
}

.detailSongItem {
  padding: 0.75rem;
  background: rgba(39, 39, 42, 0.5);
  border: 1px solid rgba(63, 63, 70, 0.3);
  border-radius: 0.5rem;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
  transition: all 0.2s ease;
}

.detailSongItem:hover {
  background: rgba(52, 52, 55, 0.7);
  color: rgba(255, 255, 255, 0.9);
}

.detailFooter {
  display: flex;
  gap: 1rem;
  align-items: center;
  padding-top: 1rem;
  border-top: 1px solid rgba(63, 63, 70, 0.3);
}

.likeCountLarge {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.6);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.thumbsUpButtonLarge {
  margin-left: auto;
  padding: 0.625rem 1.25rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 0.75rem;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 600;
  transition: all 0.2s ease;
}

.thumbsUpButtonLarge:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.4);
  color: white;
}

/* Scrollbar */
.detailPanel::-webkit-scrollbar {
  width: 4px;
}

.detailPanel::-webkit-scrollbar-track {
  background: transparent;
}

.detailPanel::-webkit-scrollbar-thumb {
  background: rgba(63, 63, 70, 0.6);
  border-radius: 10px;
}

.detailPanel::-webkit-scrollbar-thumb:hover {
  background: rgba(63, 63, 70, 0.8);
}

@media (max-width: 1024px) {
  .shareboardLayout {
    flex-direction: column;
  }

  .gridContainer {
    min-width: 100%;
  }

  .detailPanel {
    flex: 1;
    min-width: 100%;
    max-height: none;
    position: static;
  }

  .shareboardsGrid {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }

  .shareboardsGrid {
    grid-template-columns: 1fr;
  }

  .detailPanel {
    max-height: 600px;
  }

  .sectionTitle {
    font-size: 1.25rem;
  }
}
